		padding-left: 1%;
		margin-left: 9%;
		padding-right: 1%;
		background-color: rgba(0, 0, 0, 0);
		box-shadow: none;
	}
	#welcomeTextDiv {
		margin-top: auto;
		margin-bottom: auto;
		/* display: flex; */
		vertical-align: middle;
		position: relative;
	}
