@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
::-webkit-scrollbar {
  width: 0.25vw;
}
::-webkit-scrollbar-thumb {
  background: rgb(32, 32, 32);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.MuiCard-root{
  box-shadow: none!important;
}
#bioTextField{
  
}
.App {
  text-align: center;
  position: absolute;
  height: 100%;
  width: 100%;
  outline: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}


/* I need this to make hovers */
@media (max-width: 1000px) {            
	#musicContainer{
		display: none;
	}

 }
#musicContainer {
	position: fixed;
	bottom: 0;
	right: 0;
	width: 20%;
	min-width: 350;
	height: 0;
	margin-right: 5%;
	padding: 0;
	transition: all 0.6s;
	margin-bottom: -450px;
	margin-right: 0;
}
#musicContainer:hover{
	position: fixed;
	bottom: 0;
	right: 0;
	width: 20%;
	margin: 0.1%;
	/* margin-bottom: 27%; */
	padding: 0;
	transition: all 0.3s;
	margin-bottom: 0%;
}
#musicContainer:focus-within{
	position: block;
	bottom: 0;
	right: 0;
	width: 350;
	min-width: 350;
	height: 650;
	margin: 0.5%;
	margin-bottom: 1%;
	padding: 0;
	
	/* margin-bottom: 10%; */
}
body{
	overflow-x: hidden;
}
.MuiList-root::-webkit-scrollbar {
	width: 5px;
}
